<template>
  <div class="app-container">
    <van-field :value="info.name" readonly input-align="center"/>
    <van-grid :column-num="4" :border="false" class="custom-grid">
      <van-grid-item @click="toRouterUrl('/visitor/add-clean')">
        <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-2.png')" size="25" />
        <span slot="text">保洁</span>
      </van-grid-item>
      <van-grid-item @click="toRouterUrl('/visitor/add-transport')">
        <van-icon slot="icon" :name="require('@/assets/images/person/icon-grid-11.png')" size="25" />
        <span slot="text">运送</span>
      </van-grid-item>
    </van-grid>
  </div>
</template>

<script>
import Recorder from 'js-audio-recorder'

export default {
  name: 'AddCleanOrder',
  data() {
    return {
      project_code: '', // 项目编号
      type: '', // 二维码类型
      id: null,
      info: {}
    }
  },
  async mounted() {
    this.project_code = this.$route.query.code
    this.type = this.$route.query.type
    this.id = this.$route.query.id
    this.info = await this.$http.post('/visitor/get-qrcode-info', { project_code: this.project_code, type: this.type, id: this.id })
    if (this.info.full_name) {
      this.info.name = this.info.full_name
    }
  },
  methods: {
    toRouterUrl(url) {
      this.$router.push({ path: `${url}`, query: { code: this.project_code, type: this.type, id: this.id }})
    }
  }
}
</script>

<style lang="less" scoped>
.app-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #fff;
  color: rgb(153, 153, 153);
  p {
    margin: 0 0 5px 0;
  }
  .row{
    display: flex;
    justify-content: space-between;
  }
}
</style>
